import DraftMessage from "../DraftMessage";

export default class FrontendDraftMessage extends DraftMessage
{
    private static readonly messageId: string = 'newMessage';

    getTextEditors(): string[] {
        return [FrontendDraftMessage.messageId];
    }

    saveDraft($form: JQuery, useBeacon: boolean): void {
        var editor = $form.find('textarea:not(.CodeMirror textarea):eq(0)').editor(),
            messageWithoutCursorMarker = editor.getContent({withoutCursorMarker: true}),
            message = editor.getContent();

        // Update draft message variable
        this.setDraft(FrontendDraftMessage.messageId, messageWithoutCursorMarker);

        let data = {
            _token: $('meta[name=csrf_token]').prop('content'),
            ticket_number: $form.find('input[name="ticket_number"]').val(),
            is_draft: 1,
            text: message,
        };

        // Add attachments to AJAX data.
        this.addAttachmentsToData($form, data);

        if (useBeacon) {
            this.storeDraftUsingBeacon(data);
        } else {
            this.storeDraftUsingAjax($form, data);
        }
    }

    getStoreRoute(): string {
        return laroute.route('ticket.frontend.draft.store');
    }

    getDiscardRoute(): string {
        return laroute.route('ticket.frontend.draft.discard');
    }

    getDiscardData($form: JQuery): {} {
        return {
            ticket_number: $form.find('input[name="ticket_number"]').val(),
        };
    }

    discardDraft($form: JQuery): IPromise<{}>  {
        const instance = this;

        return super.discardDraft($form)
            .then(function (response: {status: 'success'|'error'} ) {
                if (response.status == 'success') {
                    const editor = $('#' + FrontendDraftMessage.messageId).editor();
                    editor.setContent('');
                    instance.setDraft(FrontendDraftMessage.messageId, editor.getContent({withoutCursorMarker: true}));
                }

                return response;
            });
    }
}
