type Editors = {
    [key: string]: {
        initialised: boolean,
        focused: boolean,
        editor: tinymce,
    };
}

export default class Editor
{
    /**
     * Key, value pair of editors which have been initialised.
     * Key is the text editor element ID. Value is bool (initialised or not).
     */
    private editors: Editors = {};

    initEditor (selector: string, $form: JQuery, opts: {}, focus: boolean = true) {
        const dfd = $.Deferred(),
            instance = this;

        if (this.isInitialised(selector)) {
            // If it's loaded but not focused once and we want to.
            if (focus && ! this.isFocused(selector)) {
                this.editors[selector]['editor'].focus();
            }

            // This helps reset the sticky toolbar which can sometimes get stuck when switching tabs.
            this.editors[selector]['editor'].fire('ResizeWindow');

            return dfd.resolve(this.editors[selector]['editor']).promise();
        }

        opts = $.extend(
            {},
            {
                setup: function (editor: tinymce) {
                    editor.on('init', function () {
                        instance.loadEditorContent(selector, $form, editor).then(function () {
                            if (focus && ! instance.isFocused(selector)) {
                                editor.focus();
                            }

                            instance.setEditor(selector, focus, editor);
                            $(selector).trigger('loaded-editor-content', [selector, $form, editor]);
                            dfd.resolve(editor);
                        });
                    });
                }
            },
            opts
        );
        const editor = $(selector).editor(opts);

        this.setEditor(selector, false, editor);

        return dfd.promise();
    };

    destroy(selector: string): void {
        if (! this.isInitialised(selector)) {
            return;
        }

        delete this.editors[selector];
        $(selector).editor().remove();
    };

    private loadEditorContent (selector: string, $form: JQuery, editor: tinymce) {
        var deferred = $.Deferred();
        var route = $(selector).data('route');
        if (typeof route !== 'string' || route === '') {
            return deferred.resolve();
        }

        $form = $form.find('.reply-form');
        $form.after('<div class="sp-editor-container"></div>');
        var $container = $form.next('.sp-editor-container');

        var $preview = $('<div class="sp-editor-preview sp-editor-content"></div>').hide();
        $container.append($preview);

        $preview.html('')
            // @ts-ignore
            .css($form.position() ?? {})
            .css('width', $form.outerWidth())
            .css('height', $form.outerHeight(true))
            .addClass('loadinggif').show();

        $.get(route)
            .done(function (json) {
                editor.setContent(json.data.purified_text);
                deferred.resolve();
            })
            .always(function () {
                $preview.hide();
            });

        return deferred.promise();
    };

    private isInitialised (selector: string): boolean {
        return this.editors.hasOwnProperty(selector)
            && this.editors[selector].hasOwnProperty('initialised')
            && this.editors[selector]['initialised'];
    }

    private isFocused (selector: string): boolean {
        return this.editors.hasOwnProperty(selector)
            && this.editors[selector].hasOwnProperty('focused')
            && this.editors[selector]['focused'];
    }

    private setEditor (selector: string, focus: boolean, editor: tinymce): void {
        this.editors[selector] = {'initialised': true, 'focused': focus, 'editor': editor};
    };
}
