function scrollToPosition(position: number): void {
    const $elm = $('.sp-alert-sticky:visible');
    let $stickyHeight = $elm.length ? -48 : 0;
    $elm.each(function() {
        const height = $(this).outerHeight();
        if (height !== undefined) {
            $stickyHeight += height;
        }
    });

    $('#content').animate({scrollTop: position + $stickyHeight - 12}, 1000);
}

export {scrollToPosition};
